<template>
  <div class="main">
    <div class="header">
      <div class="logo">xxx系统</div>
      <div class="logout" @click="logoutHandle">退出登录</div>
    </div>
    <div class="wraper">
      <div class="menu">
        <el-menu
          class="el-menu-vertical-demo"
          :default-active="activeIndex"
          :default-openeds="['system']"
          @select="handleSelect"
        >
          <el-sub-menu index="system">
            <template #title>系统管理</template>
            <el-menu-item
              v-for="item in sysMenus"
              :key="item.name"
              :index="item.name"
            >
              <span>{{ item.title }}</span>
            </el-menu-item>
          </el-sub-menu>
          <el-sub-menu index="system">
            <template #title>评分管理</template>
            <el-menu-item
              v-for="item in scoreMenus"
              :key="item.name"
              :index="item.name"
            >
              <span>{{ item.title }}</span>
            </el-menu-item>
          </el-sub-menu>
        </el-menu>
      </div>
      <div class="content">
        <div class="contentMain">
          <router-view />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from 'vue'
import { useRouter, useRoute } from 'vue-router'

export default {
  setup() {
    const route = useRoute()
    const router = useRouter()
    const activeIndex = ref(route?.name)
    const sysMenus = ref([
      { title: '菜单管理', name: 'menu' },
      { title: '角色管理', name: 'role' },
      { title: '用户管理', name: 'user' },
      { title: '登录日志', name: 'log' },
    ])
    const scoreMenus = ref([
      { title: '新增评分任务', name: 'scoreTask' },
      { title: '查看评分列表', name: 'scoreList' },
    ])

    const logoutHandle = () => {
      router.push({  name: 'home'})
    }

    const handleSelect = (name) => {
      router.push({ name })
    }

    return {
      sysMenus,
      scoreMenus,
      activeIndex,
      logoutHandle,
      handleSelect
    }
  }
}
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
